<template>
    <div class="home">
       <!-- 轮播 -->
       <div class="swiper-container" data-space-between='10'>
           <div class="swiper-wrapper">
               <div v-for="slide in slides" class="swiper-slide"><img class="slideImg" v-bind:src="slide.src" v-bind:alt="slide.alt"></div>
           </div>
           <div class="swiper-pagination"></div>
       </div>
       <!--选项卡三按钮-->
       <div class="buttons-tab">
           <a href="#tab1" class="tab-link active button">动态</a>
           <a href="#tab2" class="tab-link button">专栏</a>
           <a href="#tab3" class="tab-link button">收藏</a>
       </div>
       <div class="content">
        <!--选项卡-->
            <div class="tabs">
                <!--选项卡1：动态-->
                <div id="tab1" class="tab active">
                    <div class="card-container">
                        <div v-for="dongTai in dongTais" class="card">
                            <div class="card-content">
                                <div class="card-media">
                                    <img class="pull-left" src="http://gqianniu.alicdn.com/bao/uploaded/i4//tfscom/i3/TB10LfcHFXXXXXKXpXXXXXXXXXX_!!0-item_pic.jpg_250x250q60.jpg" width="44">
                                    <div class="card-media-body">
                                        <div class="media-name">{{dongTai.name}}</div>
                                        <div class="media-time color-gray">{{dongTai.date}}</div>
                                    </div>
                                    <div class="clear"></div>
                                </div>
                                <div class="card-content-inner">
                                    <p>{{dongTai.msg}}</p>
                                </div>
                            </div>
                            <div class="card-footer">
                                <a href="#" class="link">赞</a>
                                <a href="#" class="link">更多</a>
                            </div>
                        </div>
                    </div>
                </div>
                <!--选项卡2：文章-->
                <div id="tab2" class="tab">
                    <div class="card-container">
                        <div v-for="artcle in artcles" class="card facebook-card">
                            <div class="card-header no-border">
                                <div class="facebook-avatar"><img v-bind:src="artcle.src" width="44" height="44"></div>
                                <div class="facebook-name">{{artcle.name}}</div>
                                <div class="facebook-date">{{artcle.date}}</div>
                            </div>
                            <div class="card-content-inner">
                                <h2 class="text-left">{{artcle.title}}</h2>
                                <div class="article-con">{{artcle.msg}}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <!--选项卡3：收藏-->
                <div id="tab3" class="tab">
                    <div class="card-container">
                        <div v-for="col in cols" class="card">
                            <div class="card-header">{{col.title}}</div>
                            <div class="card-content">
                                <div class="card-content-inner">{{col.msg}}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style scoped>
    @import '../assets/css/swiper-3.4.1.min.css';
    .swiper-container{
        height: 7rem;
        position: relative;
        z-index:10;
    }
    .content{
        top:9rem;
        bottom:2.5rem;
    }
    .slideImg{
        width : 100%;
    }
    .buttons-tab{
        z-index: 10;
    }
    .card-container{
        position: relative;
    }
    .card-media{
        padding: 0.5rem 0.5rem 0 0.5rem;
    }
    .clear{
        clear:both
    }
    .media-name{
        padding-top:0.1rem;
        font-weight: bolder;
    }
    .card-media-body{
        margin-left: 2.5rem;
    }
    h2{
        margin-top: 0;
    }
    .article-con{
        text-indent: 2em;
        /*多行文本省略*/
        overflow : hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
    }
</style>

<script>
    export default {
        data () {
            return {
                slides:[
                    {src:"static/img/2.jpg",alt:""},
                    {src:"static/img/1.jpg",alt:""},
                    {src:"static/img/3.jpg",alt:""}
                ],
                dongTais:[
                    {name:"DK",date:"2017-3-12",msg:"我的第一条动态"}
                ],
                artcles:[
                    {src:"static/img/avatar.jpg",name:"DK",date:"2017-3-12",title:"Vue.js简介",
                         msg:"Vue.js（读音 /vjuː/, 类似于 view） 是一套构建用户界面的 渐进式框架。与其他重量级框架不同的是，Vue 采用自底向上增量开发的设计。Vue 的核心库只关注视图层，并且非常容易学习，非常容易与其它库或已有项目整合。另一方面，Vue 完全有能力驱动采用单文件组件和 Vue 生态系统支持的库开发的复杂单页应用。"
                    }
                ],
                cols:[
                    {
                        title:"Vue2.0路由缓存",
                        msg:"把要缓存的部分用<keep-alive></keep-alive>标签包裹起来"
                    },{
                        title:"刚进入页面就默认进入某个路由",
                        msg:"可用路由重定向解决。path: '/',redirect: '/XXX（XXX为你要默认设置的路由页面）'"
                    },{
                        title:"static文件夹与src中的assets",
                        msg:"外部文件放在src里是不能被index.html所访问到的，但是能访问到static文件夹里"
                    },{
                        title:"模块的懒加载",
                        msg:"path: '/home',component: resolve => require(['../views/Home.vue'], resolve)"
                    }
                ]
            }
        },
        mounted () {
            var mySwiper = new Swiper('.swiper-container', {
                autoplay: 2000,//可选选项，自动滑动
                loop : true,    //环路开启
                pagination : '.swiper-pagination',  //显示分页器
                paginationClickable :true,          //点击分页器控制swiper
                autoplayDisableOnInteraction : false,   //用户操作swiper后仍然自动播放
                observer:true,//修改swiper自己或子元素时，自动初始化swiper
                observeParents:true,//修改swiper的父元素时，自动初始化swiper
            })
        }
    }
</script>


